.teaser-stacklist {
  &__item {}
  &__container {
    overflow: auto;
    position: relative;
  }
  &__excerpt {
    display: inline;
  }
  &__inner {}
  &__iframe {
    overflow: auto;
    iframe {
      @media screen and (min-width: 80.000em) {
        width: 880px;
        height: 545px;
      }
    }
  }
  &__title {
    color: #fff;
    background-color: #EC4E87;
    padding: 0 10px 0;
    display: inline;
  }
}
